.app {
  display: flex;
  padding: 30px;
}

.game {
  max-width: 630px;
  max-height: 630px;
  display: flex;
  flex-wrap: wrap;
  background-color: lightblue;
  border-radius: 10px;
  padding: 0.5px;
  box-shadow: 0 0 0 1px rgb(0 0 0 / 20%);
}

.candy {
  background-color: lightblue;
  border-radius: 10px;
}

.game .game-over {
  position: absolute;
  background-color: grey;
  text-align: center;
  display: inline-block;
  opacity: 0%;
  width: 630px;
  height: 630px;
  line-height: 630px;
}

.game .game-over h2 {
  display: inline-block;
  vertical-align: middle;
}

body {
  background-color: pink;
}

.score-board {
  margin-left: 50px;
}

.values {
  color: #fff;
  font-family: Inter, sans-serif;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

#scores {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#scores td, #scores th {
  border: 1px solid #ddd;
  padding: 8px;
}

#scores tbody:nth-child(odd){background-color: #f2f2f2;}
#scores tbody:nth-child(even){background-color: #fff;}

#scores tr:hover {background-color: #ddd;}

#scores th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04AA6D;
  color: white;
}

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button {
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

@media only screen and (max-width: 910px) {
  .game {
    width: 540px;
    height: 540px;
  }
  .game .game-over {
    width: 540px;
    height: 540px;
    line-height: 540px;
  }
}

